import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  AfterViewInit, ViewEncapsulation,
} from '@angular/core';
import {LighthouseBrandListDisplayItem} from '../models/lighthouse/lighthouse-source-search';
import {ApiMode} from '../app.constants';
import {MessageBusService} from '../services/message-bus.service';
import {OrganizationConnectionCallbackEvent} from '../models/organization-connection-callback-event';
import { ElementRef, ViewChild } from '@angular/core';
import {ConfigService} from '../services/config.service';
import {VaultService} from '../services/vault.service';
import {Organization} from '../models/fasten/organization';
import {NavOutletPageName} from '../app.routing';
import {NavOutletService} from '../router/nav-outlet/nav-outlet.service';
import {EventPayload} from '../models/event-payload';

export class SourceListItem {
  brand: LighthouseBrandListDisplayItem
  searchHighlights?: string[]
}


@Component({
  // standalone: true,
  selector: 'fasten-stitch',
  templateUrl: './fasten-stitch.component.html',
  styleUrls: ['./fasten-stitch.component.css'],
  // SEE https://stackoverflow.com/questions/67115155/angular-web-component-how-to-embed-css-file-into-the-final-js-file
  // using view encapsulation to namespace CSS.
  encapsulation: ViewEncapsulation.None //default is  ViewEncapsulation.Emulated
})
export class FastenStitchComponent implements OnInit, AfterViewInit {
  @Input('public-id') publicId = ''; //validate
  @Input('reconnect-org-connection-id') reconnectOrgConnectionId: string | null = null;
  @Input('anonymous-vault-profile') anonymousVaultProfile: boolean | null = false;
  @Input('static-backdrop') staticBackdrop: boolean | null = false;

  @Output('messageBusCallback') messageBusCallback = new EventEmitter<EventPayload>()

  public errorMessage: string

  @ViewChild('stitchModalButton', { static: false }) public stitchModalButton: ElementRef;
  @ViewChild('stitchModal', { static: false }) public stitchModal: ElementRef<HTMLDialogElement>;

  // private flowbitStitchModal: Modal = null
  constructor(
    private host: ElementRef,
    public configService: ConfigService,
    private vaultService: VaultService,
    private navOutletService: NavOutletService,
    public messageBus: MessageBusService,
  ) {

    //https://stackoverflow.com/a/69173549/1157633
    this.host.nativeElement.show = this.showStitchModalExt.bind(this);
    this.host.nativeElement.hide = this.hideStitchModalExt.bind(this);
  }

  ngAfterViewInit() {}

  ngOnInit(): void {
    let publicIdParts = this.publicId.split('_')
    let apiMode = ApiMode.Test
    if(publicIdParts.length == 3 && publicIdParts[1] == ApiMode.Live) {
      apiMode = ApiMode.Live
    }
    if(publicIdParts.length != 3){
      console.error("Could not register Fasten Connect installation: missing or invalid id", this.publicId)
      this.errorMessage = 'Could not register Fasten Connect installation: missing or invalid id. Please contact the developer of this app.'
      return
    }
    console.log(this.publicId, publicIdParts, apiMode)
    //update the system config
    this.configService.systemConfig = {
      apiMode: apiMode,
      publicId: this.publicId,
      reconnectOrgConnectionId: this.reconnectOrgConnectionId,
      staticBackdrop: this.staticBackdrop,
      anonymousVaultProfile: this.anonymousVaultProfile
    }


    this.vaultService.getOrgByPublicId(this.publicId).subscribe((org: Organization) => {
      console.log("Fasten Connect registration", org)
      this.configService.systemConfig = {
        org: org
      }
    },(err) => {
      this.errorMessage = 'Could not register Fasten Connect installation using id. Please contact the developer of this app.'
      console.log("Invalid Fasten Connect registration", err)
    })

    this.messageBus.messageBusSubject.subscribe((eventPayload) => {
      console.log("bubbling up event", eventPayload)
      this.messageBusCallback.emit(eventPayload)
    })
  }

  showStitchModal(): void {

    if(this.reconnectOrgConnectionId){
      this.vaultService.getOrgConnectionById(this.publicId, this.reconnectOrgConnectionId).subscribe((orgConnection) => {
        console.log("Reconnect Org Connection", orgConnection)


        this.navOutletService.navigateByUrl(NavOutletPageName.HealthSystemConnecting, new Map<string,any>([
          ["brandId", orgConnection.catalog_brand_id],
          ["portalId", orgConnection.catalog_portal_id],
          ["endpointId", orgConnection.catalog_endpoint_id],
          ["orgConnectionId", orgConnection.org_connection_id],
          // ["externalId", this.externalId],
          // ["externalState", this.externalState],
        ]))

        this.stitchModal.nativeElement.showModal()
        this.registerDialogCloseOnBackdropClick()
      }, (err) => {
        this.errorMessage = 'Could not find the patient connection using id. Please contact the developer of this app.'
        console.log("Invalid Fasten Connect Connection ID", err)
        this.stitchModal.nativeElement.showModal()
        this.registerDialogCloseOnBackdropClick()
      })
    } else {
      if(this.anonymousVaultProfile){
        this.navOutletService.navigateByUrl(NavOutletPageName.HealthSystemSearch)
      }
      this.stitchModal.nativeElement.showModal()
      this.registerDialogCloseOnBackdropClick()
    }

  }

  // TODO: this is required because when using the showStitchModal() method, the Search component is not being rendered correctly
  // this function can be called externally to show the modal
  showStitchModalExt(): void {
    this.stitchModal.nativeElement.showModal()
    this.registerDialogCloseOnBackdropClick()
  }

  hideStitchModalExt(): void {
    this.stitchModal.nativeElement.close()
  }

  registerDialogCloseOnBackdropClick() {
    this.stitchModal.nativeElement.addEventListener('click', (event) => {
      if (this.configService.systemConfig$.staticBackdrop){
        return
      }

      var rect = this.stitchModal.nativeElement.getBoundingClientRect();
      var isInDialog = (rect.top <= event.clientY && event.clientY <= rect.top + rect.height &&
        rect.left <= event.clientX && event.clientX <= rect.left + rect.width);
      if (!isInDialog) {
        this.stitchModal.nativeElement.close();
      }
    });
  }
}
