import {Inject, Injectable} from '@angular/core';
import {VaultProfile} from '../models/fasten/vaultProfile';
import {environment} from '../../environments/environment';
import {HttpClient, HttpResponse} from '@angular/common/http';
import {ResponseWrapper} from '../models/response-wrapper';
import {BehaviorSubject} from 'rxjs';
import * as jose from 'jose'
import {ConfigService} from './config.service';

export const FASTEN_AUTH_VAULT_COOKIE_NAME = 'fasten_connect_auth_vault'

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  FASTEN_CONNECT_JWT_LOCALSTORAGE_KEY = 'fasten_connect_auth_vault';

  public IsAuthenticatedSubject = new BehaviorSubject<boolean>(false)

  constructor(
    private _httpClient: HttpClient,
    private configService: ConfigService
  ) {}




  public async VaultAuthBegin(email: string): Promise<any> {
    let resp = await this._httpClient.post<ResponseWrapper>(`${environment.connect_api_endpoint_base}/bridge/vault_auth_begin`, {
      "email": email,
    }, {withCredentials: true, params: {"public_id": this.configService.systemConfig$.publicId} }).toPromise()
    return resp
  }

  public async VaultAuthFinish(email: string, code: string): Promise<HttpResponse<any>> {
    let resp = await this._httpClient.post<HttpResponse<any>>(`${environment.connect_api_endpoint_base}/bridge/vault_auth_finish`, {
      "email": email,
      "code": code
    }, { observe: 'response', withCredentials: true, params: {"public_id": this.configService.systemConfig$.publicId} }).toPromise()
    this.setAuthToken(resp)
    return resp
  }

  public async Signout(): Promise<any> {
    this.publishAuthenticationState(false)
    //cannot delete cookie because of cross origin policies. all we can do is delete the local token in our widget.
    return localStorage.removeItem(this.FASTEN_CONNECT_JWT_LOCALSTORAGE_KEY)
  }


  public async GetJWTPayload(): Promise<any> {
    let authToken = this.GetAuthToken()
    if (!authToken) {
      return null
    }
    let jwks = jose.createRemoteJWKSet(new URL(environment.jwks_uri))
    let issuerHost = environment.connect_api_jwt_issuer_host

    try {
      //audience and issuer must be the same. This token is only valid on the fasten connect api
      const {payload, protectedHeader} = await jose.jwtVerify(authToken, jwks, {
        issuer: issuerHost,
        audience: issuerHost,
      })

      //TODO: check that the token is not expired
      // @ts-ignore
      this.configService.systemConfig = {user: payload}
      return payload
    } catch (e) {
      console.error("failed to verify jwt:", e, issuerHost)
      return null
    }
  }

  public GetAuthToken(): string {
    return localStorage.getItem(this.FASTEN_CONNECT_JWT_LOCALSTORAGE_KEY);
  }

  /////////////////////////////////////////////////////////////////////////////////////////////////
  //Private Methods
  /////////////////////////////////////////////////////////////////////////////////////////////////
  private setAuthToken(authResp: HttpResponse<any>) {
    let authHeader = authResp.headers.get("authorization")
    if (!authHeader) {
      console.warn("no auth header found in response, skipping")
      return
    }
    let token = authHeader.replace("Bearer ", "")
    localStorage.setItem(this.FASTEN_CONNECT_JWT_LOCALSTORAGE_KEY, token)
    this.publishAuthenticationState(true)
  }

  private publishAuthenticationState(authenticated: boolean){
    if(this.IsAuthenticatedSubject.value != authenticated){
      this.IsAuthenticatedSubject.next(authenticated)
    }
  }
}
