import {ChangeDetectorRef, Component, EnvironmentInjector, Injector, Input, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {ConnectHelper} from '../../utils/connect-helper';
import {VaultService} from '../../services/vault.service';
import {ConfigService} from '../../services/config.service';
import {NavOutletService} from '../../router/nav-outlet/nav-outlet.service';
import {NavOutletPage} from '../../models/nav-outlet-page';
import {NavOutletPageName} from '../../app.routing';

@Component({
  selector: 'app-health-system-connecting',
  templateUrl: './health-system-connecting.component.html',
  styleUrls: ['./health-system-connecting.component.scss']
})
export class HealthSystemConnectingComponent implements OnInit, NavOutletPage {
  @Input() brandId: string = "";
  @Input() portalId: string = "";
  @Input() endpointId: string = "";

  @Input() orgConnectionId?: string = "";
  @Input() externalId?: string = "";
  @Input() externalState?: string = "";


  constructor(
    public changeRef: ChangeDetectorRef,
    public configService: ConfigService,
    private navOutletService: NavOutletService,
    private injector: EnvironmentInjector
  ){}

  ngOnInit(): void {

    this.injector.runInContext(() => {

      ConnectHelper({
        public_id: this.configService.systemConfig$.publicId,
        brand_id: this.brandId,
        portal_id: this.portalId,
        endpoint_id: this.endpointId,
        org_connection_id: this.orgConnectionId,
        external_id: this.externalId,
        external_state: this.externalState,
        // connect_mode: this.connectMode,
      })

      //TODO: on complete, if reconnecting, we should redirect to the complete page, otherwise, we should redirect to the dashboard
    })
  }
  markForCheck(){
    this.changeRef.markForCheck()
  }

  cancelAccountConnect(){

    this.navOutletService.navigateByUrl(NavOutletPageName.Dashboard)
  }
}
