import { Type} from '@angular/core';
import {VaultProfileSigninComponent} from './pages/vault-profile-signin/vault-profile-signin.component';
import {VaultProfileSigninCodeComponent} from './pages/vault-profile-signin-code/vault-profile-signin-code.component';
import {IdentityVerificationComponent} from './pages/identity-verification/identity-verification.component';
import {
  FormHealthSystemRequestComponent
} from './pages/form-health-system-request/form-health-system-request.component';
import {FormSupportRequestComponent} from './pages/form-support-request/form-support-request.component';
import {HealthSystemSearchComponent} from './pages/health-system-search/health-system-search.component';
import {
  HealthSystemBrandDetailsComponent
} from './pages/health-system-brand-details/health-system-brand-details.component';
import {HealthSystemConnectingComponent} from './pages/health-system-connecting/health-system-connecting.component';
import {CompleteComponent} from './pages/complete/complete.component';
import {NavOutletPage} from './models/nav-outlet-page';
import {DashboardComponent} from './pages/dashboard/dashboard.component';
import {IsAuthenticatedAuthGuard} from './auth-guards/is-authenticated-auth-guard';
import {AuthService} from './services/auth.service';
import {NavOutletService} from './router/nav-outlet/nav-outlet.service';

export enum NavOutletPageName {
  Default = "",

  VaultProfileSignin = "vault-profile-signin",
  VaultProfileSigninCode = "vault-profile-signin-code",
  IdentityVerification = "identity-verification",

  Dashboard = "dashboard",
  FormHealthSystemRequest = "form-health-system-request",
  FormSupportRequest = "form-support-request",

  HealthSystemSearch = "health-system-search",
  HealthSystemBrandDetails = "health-system-brand-details",
  HealthSystemConnecting = "health-system-connecting",
  CompleteSummary = "complete",
}

export interface NavOutletCanActivate {
  (nextRoute: NavOutletRoute): Promise <boolean>
}
export class NavOutletRoute {
  page: string = NavOutletPageName.Dashboard
  component: Type<NavOutletPage> = null
  inputs?: Map<string, any>
  canActivate?: NavOutletCanActivate
}

export const Routes: NavOutletRoute[] = [
  //on empty, this is where we go
  { page: NavOutletPageName.Default, component: VaultProfileSigninComponent },
  // { page: NavOutletPageName.Default, component: HealthSystemSearchComponent },

  { page: NavOutletPageName.VaultProfileSignin, component: VaultProfileSigninComponent },
  { page: NavOutletPageName.VaultProfileSigninCode, component: VaultProfileSigninCodeComponent },
  { page: NavOutletPageName.IdentityVerification, component: IdentityVerificationComponent, canActivate: IsAuthenticatedAuthGuard },

  { page: NavOutletPageName.Dashboard, component: DashboardComponent, canActivate: IsAuthenticatedAuthGuard },
  { page: NavOutletPageName.HealthSystemSearch, component: HealthSystemSearchComponent, canActivate: IsAuthenticatedAuthGuard },
  { page: NavOutletPageName.HealthSystemBrandDetails, component: HealthSystemBrandDetailsComponent, canActivate: IsAuthenticatedAuthGuard },

  //cannot be authenticated, must be publically accessible for reconnecting
  { page: NavOutletPageName.HealthSystemConnecting, component: HealthSystemConnectingComponent },
  { page: NavOutletPageName.CompleteSummary, component: CompleteComponent },
  { page: NavOutletPageName.FormHealthSystemRequest, component: FormHealthSystemRequestComponent },
  { page: NavOutletPageName.FormSupportRequest, component: FormSupportRequestComponent },
]
