import {Directive, ElementRef, HostListener, Input, ViewContainerRef} from '@angular/core';
import {NavOutletService} from './nav-outlet.service';

@Directive({
  selector: '[navOutletLink]'
})
export class NavOutletLinkDirective {

  constructor(
    private eleRef: ElementRef,
    public navOutletService: NavOutletService
  ) {}

  @Input() navOutletLink: string = ""

  @HostListener('click') onClick(){
    this.navOutletService.navigateByUrl(this.navOutletLink)
  }
}
