/**
 * AppConfig interface. Update this interface to strictly type your config
 * object.
 */
import {ApiMode} from '../app.constants';
import {Organization} from './fasten/organization';
import {VaultProfile} from './fasten/vaultProfile';
import {SearchFilter} from './search-filter';
import {
  LighthouseBrandListDisplayItem,
  LighthouseEndpointListDisplayItem,
  LighthousePortalListDisplayItem
} from './lighthouse/lighthouse-source-search';
import {RecordLocatorFacility} from './fasten/record-locator-facility';

export interface VaultSystemConfig {
  publicId: string;
  apiMode: ApiMode;
  org?: Organization;
  org_id?: string
  reconnectOrgConnectionId?: string;

  staticBackdrop?: boolean; //default false; if true, the modal will not close when clicking outside of the modal or pressing the ESC key.
  anonymousVaultProfile?: boolean; //default false; if true, the user is not required to be logged in and the vault profile is anonymous.
}

// There are 3 statuses for a patient account:
// - Connected - the patient account is fully authorized and Fasten can access medical records from this institution
// - Pending - the patient has specified they have records at this institution, but Fasten has not yet been able to access them.
//             the patient must complete the connection process by logging into their account at the institution.
// - Available - these are facilities that have been found via a record locator search. The patient has not yet agreed to share medical records from this institution.
//             - this status is similar to "pending" for display purposes.
export class VaultProfileConfig {
  vaultProfile?: VaultProfile;

  // connected patient accounts
  connectedPatientAccounts?: {
    org_connection_id: string,
    connection_status: string,
    platform_type: string,

    brand: LighthouseBrandListDisplayItem,
    portal: LighthousePortalListDisplayItem,
    endpoint: LighthouseEndpointListDisplayItem
  }[];

  // pending patient accounts
  pendingPatientAccounts?: { brand: LighthouseBrandListDisplayItem, portal: LighthousePortalListDisplayItem, endpoint: LighthouseEndpointListDisplayItem }[];

  // available facilities -- found via record locator search
  availableFacilities?: RecordLocatorFacility[];


  addPendingAccount(brand: LighthouseBrandListDisplayItem, portal: LighthousePortalListDisplayItem, endpoint: LighthouseEndpointListDisplayItem): void {
    if (!this.pendingPatientAccounts) {
      this.pendingPatientAccounts = []
    }
    this.pendingPatientAccounts?.push({brand, portal, endpoint})
  }

  addConnectedAccount(org_connection_id: string, connection_status: string, platform_type: string, brand_id: string, portal_id: string, endpoint_id: string): void {
    if (!this.connectedPatientAccounts) {
      this.connectedPatientAccounts = []
    }

    //find the newly connected account metadata from the pending list and move it to the connected list
    let pendingIndex = this.pendingPatientAccounts?.findIndex((pendingAccount) => {
      return pendingAccount.brand.id == brand_id && pendingAccount.portal.id == portal_id && pendingAccount.endpoint.id == endpoint_id
    })

    if (pendingIndex != -1) {
      let pendingAccount = this.pendingPatientAccounts?.splice(pendingIndex, 1)[0]
      this.connectedPatientAccounts?.push({org_connection_id, connection_status, platform_type, brand: pendingAccount.brand, portal: pendingAccount.portal, endpoint: pendingAccount.endpoint})
    } else {
      //TODO: lookup the brand, portal, endpoing information by ID from the Lighthouse API, and add it to the connected Patients account list.
      console.error("NOT SUPPORTED: could not find the brand, portal, endpoint information by ID")
    }
  }


  addAvailableRecordLocatorAccount(recordLocatorFacility: RecordLocatorFacility): void {
    if (!this.availableFacilities) {
      this.availableFacilities = []
    }
    this.availableFacilities?.push(recordLocatorFacility)
  }
}


export interface VaultSearchConfig {
  searchFilter?: SearchFilter
  selectedBrand?: LighthouseBrandListDisplayItem
}

// - apiMode
// - organization information
// - org credential information
// - reconnect id.

/**
 * Default configuration for the entire application. This object is used by
 * "PortalConfigService" to set the default configuration.
 */
export const vaultConfigDefaults: VaultSystemConfig = {
  publicId: '',
  apiMode: ApiMode.Test
};
