import {inject, Injectable} from '@angular/core';
import {AuthService} from '../services/auth.service';
import {NavOutletPageName, NavOutletCanActivate, NavOutletRoute} from '../app.routing';
import {NavOutletLinkDirective} from '../router/nav-outlet/nav-outlet-link.directive';
import {NavOutletService} from '../router/nav-outlet/nav-outlet.service';
import {ConfigService} from '../services/config.service';

export function IsAuthenticatedAuthGuard(nextRoute: NavOutletRoute): Promise <boolean> {
  const authService = inject(AuthService);
  const navOutletService = inject(NavOutletService);
  const configService = inject(ConfigService);

  if(configService.systemConfig$.anonymousVaultProfile){
    return Promise.resolve(true) //skip auth check
  }

  return authService.GetJWTPayload()
      .then(jwtPayload => {
        //check if the user is authenticated, if not, redirect to login
        if (!jwtPayload) {
          if (nextRoute.page === NavOutletPageName.VaultProfileSignin) {
            return true
          } else {
            console.log("User is not authenticated, redirecting to login page")
            navOutletService.navigateByUrl(NavOutletPageName.VaultProfileSignin);
            return false
          }
        } else if(!jwtPayload.has_verified_identity){
          if(nextRoute.page === NavOutletPageName.IdentityVerification){
            return true
          } else {
            console.log("Profile does not have a verified identity, redirecting to id verification step", jwtPayload)
            navOutletService.navigateByUrl(NavOutletPageName.IdentityVerification);
            return false
          }
        }
        // continue as normal
        return true
      })
      .catch(err => {
        console.error("Error checking if user is authenticated", err)
        navOutletService.navigateByUrl(NavOutletPageName.VaultProfileSignin);
        return false
      })
}
